.popup__submit-button {
  display: block;
  width: 100%;
  height: 50px;
  background-color: #000;
  border-radius: 2px;
  border: none;
  padding: 0;
  cursor: pointer;
  transition: opacity 0.2s ease;
  color: #FFF;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
}

.popup__submit-button:hover {
  opacity: 0.8;
}

@media screen and (max-width: 767px) {
  .popup__submit-button {
    font-size: 14px;
    line-height: 17px;
    height: 46px;
  }
}
